<template>
<div>
  <div class="search">
    <el-input v-model="input" placeholder="搜索歌曲、歌手、专辑"></el-input>
  </div>
  <ul class="suggest">
    <li v-for="(item, index) in suggestlist" :key="index">
      <suggestlist v-bind:item="item" v-bind:currentPlayList="suggestlist"></suggestlist>
    </li>
  </ul>
</div>
</template>
<script>
import suggestlist from "@/components/suggestlist.vue";
export default {
  components: {
    suggestlist
  },
  data() {
    return {
      input: "",
      suggest:[],
    }
  },
    computed: {
        suggestlist: function() {
        return this.suggest.slice(0, 20);
        }
    },
  created() {

  },
  // props: ["item","currentPlayList"],
  methods: {
    getSongDetail: function(parmas) {
    axios
    .get("/song/url", {
        params: {
        id: parmas.id
        }
    })
    .then(res => {
        // console.log(res.data.data[0]);
        if (res.data.data[0].url == null) {
        } else {
        this.$store.commit("updateCurrentPlay", res.data.data[0]);
        this.$store.commit("updateCurrentPlayDetail", parmas);
        }
    })
    .catch(err => {
        console.error(err);
    });
    },
    play: function(obj) {
      console.log(obj);
      this.getSongDetail(obj);
      this.$store.commit("updateCurrentPlayDetail", obj);
      this.$store.commit("updataCurrentPlayList", this.currentPlayList);
      // console.log(this.currentPlayList);
      this.$store.state.isplaying=true
    },
    getSuggest: function(x) {
      return axios
        .get("/search", {
          params: {
            keywords: x,
            // type: "mobile"
          }
        })
        .then(res => {
          console.log(res.data.result.songs);
          this.suggest = res.data.result.songs;
        })
        .catch(err => {
          console.error(err);
        });
    },
  },
    watch: {
    input: function(newValue, oldValue) {
      if (newValue) {
        if (!this.isClick) {
          this.getSuggest(newValue);
        }
        console.log(newValue);
      } else {
        this.suggest = [];
      }
    }
  }
}
</script>
<style lang="less">
.search{
    width: 100%;
    height: 40px;
    background: #ebecec;
    border-radius: 30px;
    text-align: center;
    margin: 20px 0;
    border-bottom: 1px solid #ebecec;
    .el-input__inner{
        width: 90%;
        // height: 100%;
        background-color: #ebecec !important;
        border: none;
        color: #000;
    }
}
.suggest{
    width: 100%;
    padding: 0;
    margin: 0;
    // position: relative;
    // top: -20px;
    li{
        border-bottom: 1px solid #eee;
        padding: 0px 30px;
    }
}
</style>