<template>
  <div class="hotlist">
    <div class="hot-img">
      <img :src="img.coverImgUrl" alt>
    </div>
      
    <div class="hotsong">
      <li v-for="(item, index) in Hot" :key="index">
        <Hotsong v-bind:item="item" v-bind:currentPlayList="Hot"></Hotsong>
      </li>
    </div>
  </div>
</template>
<script>
import Hotsong from "@/components/Hotsong.vue";
import BScroll from 'better-scroll'
export default {
    computed: {
        Hot: function() {
        return this.list.slice(0, 30);
        }
    },
    name: 'hotmusic',
    components: {
        Hotsong
    },
    data() {
    return {
        list:[],
        img:[]
    }
    },
    created() {
    this.$store.commit("updateFullscreenLoadingTrue");
    axios
    .get("/top/list", {
        params: {
          idx: 1
        }
      })
    .then(res => {
        console.log(res.data.playlist.tracks);
        this.list = res.data.playlist.tracks;
        this.img=res.data.playlist;
        this.$store.commit("updateFullscreenLoadingFalse");
      })
      .catch(err => {
        // console.error(err);
      });
  },
}
</script>
<style lang="less">
.hotsong{
  li{
    padding: 0 10px;
    border-bottom: 1px solid #eee;      
  }  
}
.hot-img{
  width: 100%;
  height: 150px;
  overflow: hidden;
  position: relative;
  img{
    width: 100%;
    position: absolute;
    // top: -50%;
    transform: translateY(-30%)
  }
}
</style>