<template>
<div @click="play(item)">
  <p class="NewSong">{{item.name}}</p>
  <p class="art">{{item.al.name}}</p>
  <!-- <p v-else class="art">{{item.artists[0].name}}</p> -->
  <span></span>
</div>
</template>
    
<script>
export default {
  props: ["item","currentPlayList"],
  methods: {
    getSongDetail: function(parmas) {
    axios
    .get("/song/url", {
        params: {
        id: parmas.id
        }
    })
    .then(res => {
        console.log(res.data.data[0]);
        if (res.data.data[0].url == null) {
        } else {
        this.$store.commit("updateCurrentPlay", res.data.data[0]);
        this.$store.commit("updateCurrentPlayDetail", parmas);
        }
    })
    .catch(err => {
        console.error(err);
    });
    },
    play: function(obj) {
      console.log(obj);
      this.getSongDetail(obj);
      this.$store.commit("updataCurrentPlayList", this.currentPlayList);
      // console.log(this.currentPlayList);
      this.$store.state.isplaying=true
    }
  },
}
</script>
