<template>
<div class="list">
    <div class="back"><img @click="back" src="../../返回.svg"  alt=""></div>
    <div>
    <ul>
        <li v-for="(item, index) in list" :key="index">
      <list v-bind:item="item" v-bind:currentPlayList="list"></list>
  </li>
    </ul>        
    </div>

  
  <!-- <p class="art">{{item.artists[0].name}}</p> -->
  <span></span>
</div>
</template>

<script>
import list from "@/components/list.vue";
export default {
  name: 'music',
  components: {
    list
  },
  data() {
  return {
    list:null,
    }
  },
  created() {
    this.$store.commit("updateFullscreenLoadingTrue");
    axios
      .get("/playlist/detail", {
        params: {
          id: this.$route.query.id
        }
      })
      .then(res => {
        console.log(res.data.playlist.tracks);
        this.list = res.data.playlist.tracks;
        this.$store.commit("updateFullscreenLoadingFalse");
      })
      .catch(err => {
        console.error(err);
      });
  },
  methods: {
    back(){
      this.$router.go(-1)
    }
  },
}
</script>
<style lang="less">
.list{
    position: absolute;
    top: 0;
    width: 100%;
    ul{
        background: white;
        margin: 0;
        padding: 0;
        position: relative;
        // top: -16px;
        li{
            border-bottom: 1px solid #eee;
             padding: 0 10px;
            div{
                padding: 1px 0;
            }
        }
    }
  .back{
        width: 100%;
        height: 40px;
        background: #d43c33;
        text-align: left;
        img{
          height: 65%;
          line-height: 40px;
          position: relative;
          top: 17%;
        }
    }  
}
    
</style>